.ye-markdown-hints {
  min-width: 210px;
  line-height: var(--g-text-code-1-line-height);
}
.ye-markdown-hints__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 4px;
  gap: 4px;
}
.ye-markdown-hints__title {
  font-weight: 500;
}
.ye-markdown-hints__hint {
  white-space: nowrap;
  color: var(--g-color-text-complementary);
}
.ye-markdown-hints__title, .ye-markdown-hints__hint {
  flex: 1;
  text-align: left;
}
.ye-markdown-hints__docs-link {
  display: inline-block;
  margin-top: 8px;
}