.g-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  height: 100%;
}
.g-container_mw_s {
  max-width: 576px;
}
.g-container_mw_m {
  max-width: 768px;
}
.g-container_mw_l {
  max-width: 1080px;
}
.g-container_mw_xl {
  max-width: 1200px;
}
.g-container_mw_xxl {
  max-width: 1400px;
}
.g-container_mw_xxxl {
  max-width: 1920px;
}
