.df-transparent {
  display: flex;
  margin-bottom: 15px;
}
.df-transparent:last-child {
  margin-bottom: 0;
}
.df-transparent_array-item {
  max-width: 338px;
}
.df-transparent_without-max-width {
  max-width: none;
  max-width: initial;
}
.df-transparent_without-max-width > .df-error-wrapper {
  width: auto;
}
.df-transparent__remove-button {
  margin-left: 5px;
}