.ye-markup-editor,
.ye-markup-preview {
  display: flex;
  flex: 1 0 0;
  flex-direction: column;
}
.ye-markup-editor__toolbar,
.ye-markup-preview__toolbar {
  flex-shrink: 0;
}
.ye-markup-editor__editor,
.ye-markup-preview__editor {
  flex-basis: 100%;
  min-height: 36px;
  max-height: 100%;
  cursor: text;
}
.ye-markup-editor__editor_toolbar-visible,
.ye-markup-preview__editor_toolbar-visible {
  max-height: calc(100% - 28px);
}
.ye-markup-editor .CodeMirror-lines,
.ye-markup-preview .CodeMirror-lines {
  padding: 4px 0 0;
}
.ye-markup-editor_toolbar .CodeMirror-lines,
.ye-markup-preview_toolbar .CodeMirror-lines {
  padding-top: 16px;
}
.ye-markup-editor .focus::-moz-selection, .ye-markup-preview .focus::-moz-selection {
  background: rgba(98, 146, 255, 0.2);
}
.ye-markup-editor .CodeMirror-focused .CodeMirror-selected,
.ye-markup-editor .focus::selection,
.ye-markup-preview .CodeMirror-focused .CodeMirror-selected,
.ye-markup-preview .focus::selection {
  background: rgba(98, 146, 255, 0.2);
}
.ye-markup-editor ::-moz-selection, .ye-markup-preview ::-moz-selection {
  background: rgba(0, 30, 98, 0.2);
}
.ye-markup-editor .CodeMirror-selected,
.ye-markup-editor ::selection,
.ye-markup-preview .CodeMirror-selected,
.ye-markup-preview ::selection {
  background: rgba(0, 30, 98, 0.2);
}
.ye-markup-editor .CodeMirror,
.ye-markup-preview .CodeMirror {
  height: 100%;
  min-height: 100%;
  color: var(--g-color-text-primary);
  font-family: var(--g-font-family-monospace);
  font-size: var(--g-text-code-2-font-size);
  line-height: var(--g-text-code-2-line-height);
  font-weight: var(--g-text-code-font-weight);
}
.ye-markup-editor .CodeMirror .CodeMirror-cursor,
.ye-markup-preview .CodeMirror .CodeMirror-cursor {
  border-color: var(--g-color-text-primary);
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-header,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-header {
  color: inherit;
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-quote,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-quote {
  color: var(--g-color-text-positive);
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-link,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-link {
  color: var(--g-color-text-link);
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-string,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-string {
  color: var(--g-color-text-warning-heavy);
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-comment,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-comment {
  color: var(--g-color-text-misc);
}
.ye-markup-editor .CodeMirror .cm-m-markdown.cm-variable-2, .ye-markup-editor .CodeMirror .cm-m-markdown.cm-variable-3, .ye-markup-editor .CodeMirror .cm-m-markdown.cm-keyword,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-variable-2,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-variable-3,
.ye-markup-preview .CodeMirror .cm-m-markdown.cm-keyword {
  color: inherit;
}
.ye-markup-editor .CodeMirror.cm-s-default,
.ye-markup-preview .CodeMirror.cm-s-default {
  background-color: transparent;
}
.ye-markup-editor .CodeMirror.cm-s-default .CodeMirror-placeholder,
.ye-markup-preview .CodeMirror.cm-s-default .CodeMirror-placeholder {
  color: var(--g-color-text-secondary);
}

.ye-markup-preview__outer {
  overflow-y: auto;
  flex: 1 0 0;
}
.ye-markup-preview__outer_vertical {
  box-sizing: border-box;
  margin-top: 6px;
  padding-top: 12px;
  border-top: 8px solid var(--g-color-base-generic-ultralight);
}
.ye-markup-preview__html {
  position: relative;
}
.ye-markup-preview__preview-sign {
  margin-bottom: 8px;
}