.df-card {
  margin-bottom: 20px;
}
.df-card:last-child {
  margin-bottom: 0;
}
.df-card .df-row {
  width: 100%;
  max-width: none;
  max-width: initial;
}
.df-card .df-row .df-select,
.df-card .df-row .df-multi-select,
.df-card .df-row .df-transparent_array-item {
  max-width: none;
  max-width: initial;
}
.df-card .df-monaco-input,
.df-card .df-monaco-base-view {
  width: 100%;
}
.df-card_empty-body .df-card__header {
  cursor: default;
}
.df-card_empty-body .df-card__header-right {
  display: none;
}
.df-card_empty-body .df-card__body {
  display: none;
}
.df-card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.df-card__header_interactive {
  cursor: pointer;
}
.df-card__header-left {
  display: flex;
  align-items: center;
  flex-grow: 1;
}
.df-card__header-right {
  display: flex;
  align-items: center;
}
.df-card__header-right > * {
  margin-left: 10px;
}
.df-card__title {
  display: flex;
  max-width: 533px;
}
.df-card__note {
  margin-left: 5px;
}
.df-card__note .gc-help-popover {
  display: flex;
}
.df-card__note .gc-help-popover > span {
  display: flex;
}
.df-card__toggler-icon_open {
  transform: rotate(180deg);
}
.df-card__body {
  display: none;
  padding: 0 20px 20px;
}
.df-card__body_open {
  display: block;
}