.ye-base-tooltip {
  display: flex;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 0;
}
.ye-base-tooltip__vertical-line {
  min-height: 100%;
  max-height: 100vh;
  margin: 0 7px;
  border-style: ridge;
}
.ye-base-tooltip__remove-button {
  display: flex;
  align-items: center;
  min-height: 100%;
}
.ye-base-tooltip__content__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ye-base-tooltip__content__row:not(:last-child) {
  margin-bottom: 4px;
}
.ye-base-tooltip__content__input {
  width: 180px;
  margin-left: 8px;
}
.ye-base-tooltip__content__number {
  max-width: 150px;
}
.ye-base-tooltip__content__switch {
  margin-right: 28px;
}