.ye-emoji-suggest__list.g-list {
  width: 256px;
}
.ye-emoji-suggest__list.g-list .g-list__empty-placeholder {
  height: 28px;
  padding-right: 12px;
  padding-left: 12px;
}
.ye-emoji-suggest__list-item {
  cursor: pointer;
}
.ye-emoji-suggest__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 16px;
}
.ye-emoji-suggest__item-info {
  display: flex;
  align-items: center;
}
.ye-emoji-suggest__item-icon {
  display: flex;
  flex: 0 0 auto;
  justify-content: center;
  width: 20px;
  margin-right: 8px;
  font-size: var(--g-text-body-3-font-size);
  line-height: var(--g-text-body-3-line-height);
  font-weight: var(--g-text-body-font-weight);
}
.ye-emoji-suggest__item-name {
  margin-right: 12px;
  white-space: nowrap;
}
.ye-emoji-suggest__item-shortcuts {
  display: inline-flex;
  align-items: center;
  -moz-column-gap: 4px;
  column-gap: 4px;
}
.ye-emoji-suggest__item-shortcut {
  padding: 1px 2px;
  white-space: nowrap;
  border-radius: var(--g-border-radius-xs);
  background-color: var(--g-color-base-generic);
  font-family: var(--g-font-family-monospace);
  font-size: var(--g-text-code-inline-1-font-size);
  line-height: var(--g-text-code-inline-1-line-height);
  font-weight: var(--g-text-code-font-weight);
}