.ye-form-row {
  display: flex;
  gap: 0 12px;
}
.ye-form-row__label {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  min-width: 128px;
}
.ye-form-row__label-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ye-form-row__label-help {
  margin-left: 6px;
}
.ye-form-row__control {
  flex-grow: 1;
  min-width: 200px;
}