.ye-toolbar-list-button_arrow {
  width: 42px;
}
.ye-toolbar-list-button_arrow .g-button__text.g-button__text.g-button__text {
  margin: 0px 21px;
}
.ye-toolbar-list-button_arrow .g-button__icon_side_start {
  left: -1px;
}
.ye-toolbar-list-button_arrow .g-button__icon_side_end {
  right: -1px;
}
.ye-toolbar-list-button__item {
  display: flex;
  justify-content: space-between;
  -moz-column-gap: 24px;
  column-gap: 24px;
}
.ye-toolbar-list-button__extra {
  display: flex;
  align-items: center;
  -moz-column-gap: 8px;
  column-gap: 8px;
}
.ye-toolbar-list-button__extra .g-icon {
  display: block;
}
.ye-toolbar-list-button__hint {
  display: flex;
  align-items: center;
}
.ye-toolbar-list-button__hint span {
  display: flex;
}
.ye-toolbar-list-button__action-disabled-popover {
  display: block;
}
.ye-toolbar-list-button__action-disabled-popover .g-popover__handler {
  display: block;
}
.ye-toolbar-list-button__action-disabled-tooltip {
  min-height: -moz-fit-content;
  min-height: fit-content;
  padding: 6px 12px;
}