.ye-upload-widget {
  display: flex;
  justify-content: space-between;
  padding: 4px 6px;
  border-radius: var(--g-border-radius-xs);
  background-color: var(--g-color-base-misc-light);
}
.ye-upload-widget__labels {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2px;
}
.ye-upload-widget__close-button {
  margin-left: 4px;
}

.ye-upload-label__content {
  display: flex;
  align-items: center;
  -moz-column-gap: 4px;
  column-gap: 4px;
}
.ye-upload-label__filename {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 128px;
}