.split-view {
  display: flex;
  gap: 30px;
}
@media (max-width: 768px) {
  .split-view {
    flex-direction: column;
  }
}

.area__card {
  padding: 10px 25px;
}
.area__card > section {
  height: var(--diplodoc-playground-height);
  min-height: var(--diplodoc-playground-height);
  max-height: var(--diplodoc-playground-height);
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0;
}

/* yfm preview */
.area__yfm {
  border-radius: 2px;
  height: calc(var(--diplodoc-playground-height) + 26px);
  min-height: calc(var(--diplodoc-playground-height) + 26px);
  max-height: calc(var(--diplodoc-playground-height) + 26px);
  overflow-y: scroll;
  overflow-x: hidden;
}

.area-card__editor {
  border-radius: 2px;
  padding-right: 0px;
}

.area__tabs {
  text-transform: uppercase;
}

.input {
  width: 50%;
  margin-top: 39px;
}
@media (max-width: 768px) {
  .input {
    width: 100%;
  }
}

.output {
  width: 50%;
}
@media (max-width: 768px) {
  .output {
    width: 100%;
  }
}
