.ye-table-view__hack-strip {
  position: absolute;
  left: -13px;
  display: block;
  width: 13px;
  height: 100%;
  background: transparent;
}
.ye-table-view__wrapper {
  margin-bottom: 18px !important;
}
.ye-table-view__wrapper {
  position: relative;
  display: inline-block;
  max-width: calc(100% - 18px);
  margin-right: 18px;
  /* stylelint-disable declaration-no-important */
}
.ye-table-view__buttons-visible .ye-table-view__plus-button-controls {
  pointer-events: initial;
  opacity: 1;
}
.ye-table-view__plus-button-controls {
  pointer-events: none;
  opacity: 0;
  transition: opacity 150ms ease 50ms;
}
.ye-table-view__plus-button-controls_right {
  position: absolute;
  z-index: 100;
  top: 0;
  right: -18px;
  display: flex;
  align-items: center;
  width: 16px;
  height: 100%;
}
.ye-table-view__plus-button-controls_bottom {
  position: absolute;
  z-index: 100;
  bottom: -18px;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 16px;
}
.ye-table-view__plus-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.ye-table-view__plus-button_right {
  width: 16px;
  height: 100%;
}
.ye-table-view__plus-button_bottom {
  width: 100%;
  height: 16px;
}

.ye-table-view.ye-table-view {
  background: transparent !important;
}

.ye-table-view.ye-table-view {
  z-index: auto;
  z-index: initial;
  display: block;
  overflow: auto;
  box-sizing: content-box;
  width: -moz-fit-content;
  width: fit-content;
  margin: -13px 0 0 -13px;
  padding: 13px 0 0 13px;
  border: none;
  border-radius: 0;
}
.ye-table-view.ye-table-view tbody {
  display: block;
  border-radius: 8px;
  background: var(--g-color-base-background);
  box-shadow: inset 0 0 0 1px var(--g-color-line-generic);
}

.ye-table-wrapper {
  display: inline-block;
  margin-right: 2px;
}