.df-view-row {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}
.df-view-row:last-child {
  margin-bottom: 0;
}
.df-view-row__left {
  width: 300px;
  max-width: 300px;
  min-width: 300px;
  display: flex;
  align-items: baseline;
  overflow: hidden;
}
.df-view-row__dots {
  min-width: 40px;
  flex-grow: 1;
  margin: 0 2px;
  border-bottom: 1px dotted var(--g-color-text-secondary);
}
.df-view-row__right {
  max-width: 480px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.df-view-row__right > .df-view-transparent {
  margin-bottom: 6px;
}
.df-view-row__right > .df-view-transparent:last-child {
  margin-bottom: 0;
}
.df-view-row:hover > .df-copy-button {
  display: block;
}