.ProseMirror .yfm-tabs {
  border: 1px dashed transparent;
  border-radius: var(--g-border-radius-s);
}
.ProseMirror .yfm-tabs:hover {
  border-color: var(--g-color-line-generic);
}
.ProseMirror .ye-yfm-tab__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  padding-bottom: 7px;
  border-bottom: 2px solid transparent;
  background-clip: padding-box;
}
.ProseMirror .ye-yfm-tab__remove-button {
  display: flex;
  width: 15px;
  min-width: 15px;
  height: 15px;
  margin-left: 9px;
  color: var(--g-color-base-generic-medium);
}
.ProseMirror .ye-yfm-tab__remove-button:hover {
  color: var(--g-color-text-primary);
}
.ProseMirror .ye-yfm-tab__create-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  min-width: 22px;
  height: 22px;
  min-height: 22px;
  margin-bottom: 9px;
  opacity: 0;
  color: var(--g-color-text-primary);
}
.ProseMirror .ye-yfm-tab__create-button:hover {
  border-radius: var(--g-border-radius-xs);
  background: var(--g-color-base-generic-hover);
}
.ProseMirror .yfm-tab {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 3px;
  padding-bottom: 0;
  border-bottom: none;
}
.ProseMirror .yfm-tab .ye-placeholder {
  color: var(--g-color-text-hint);
}
.ProseMirror .yfm-tab[data-diplodoc-is-active=true] .ye-yfm-tab__wrapper {
  border-bottom-color: #027bf3;
}
.ProseMirror .yfm-tab:hover .ye-yfm-tab__create-button, .ProseMirror .yfm-tab:last-child .ye-yfm-tab__create-button {
  opacity: 1;
}