.ye-editor-component {
  display: flex;
  height: 100%;
}
.ye-editor-component_split_vertical {
  display: grid;
  grid-template-rows: 1fr auto 1fr;
  grid-gap: 2px;
  gap: 2px;
}
.ye-editor-component__editor {
  flex-grow: 1;
  gap: 2px;
  min-width: 0;
}
.ye-editor-component__editor-wrapper {
  display: flex;
  width: 100%;
}
.ye-editor-component__preview-wrapper {
  overflow-y: auto;
  width: 100%;
}
.ye-editor-component__resizer {
  margin: 0 5px;
}
.ye-editor-component__resizer_horizontal {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-width: 8px;
  min-height: 100%;
  margin: 0 8px;
  cursor: col-resize;
  background: var(--g-color-base-generic-ultralight);
}
.ye-editor-component__gutter {
  top: 50%;
  bottom: 50%;
  width: 2px;
  height: 24px;
  background: var(--g-color-line-generic);
}
.ye-editor-component .ye-toolbar-list-button_arrow {
  width: 42px;
}
.ye-editor-component .in-resize {
  pointer-events: none;
}