.ye-toolbar-select__item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}
.ye-toolbar-select__item-icon {
  display: flex;
  flex-shrink: 0;
  margin-right: 6px;
}
.ye-toolbar-select__item-content {
  display: flex;
  justify-content: space-between;
  -moz-column-gap: 16px;
  column-gap: 16px;
  width: 100%;
}
.ye-toolbar-select__item-extra {
  display: flex;
  align-items: center;
  -moz-column-gap: 8px;
  column-gap: 8px;
}
.ye-toolbar-select__item-hint {
  display: flex;
  align-items: center;
}
.ye-toolbar-select__item-hint span {
  display: flex;
}