.df-toggler-card {
  width: 254px;
  padding: 10px;
  height: 88px;
}
.df-toggler-card__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.df-toggler-card__text {
  margin-top: 12px;
  display: block;
  margin-right: 15px;
  height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
}