/* use this for style redefinitions to awoid problems with
unpredictable css rules order in build */
.pc-author {
  font-size: var(--g-text-body-3-font-size);
  line-height: var(--g-text-body-3-line-height);
  display: flex;
  flex-direction: column;
}
.pc-author__avatar {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  border-radius: 16px;
  overflow: hidden;
}
.pc-author__avatar img {
  width: 80px;
  height: 80px;
  -o-object-fit: cover;
  object-fit: cover;
  display: block;
}
.pc-author__name {
  font-size: var(--g-text-body-3-font-size);
  line-height: var(--g-text-body-3-line-height);
}
.pc-author__description {
  font-size: var(--g-text-body-2-font-size);
  line-height: var(--g-text-body-2-line-height);
  color: var(--g-color-text-secondary);
  padding-top: 4px;
}
.pc-author__label:not(:first-child) {
  font-weight: 400;
  margin-top: 8px;
}
.pc-author_type_column {
  z-index: 1;
  position: relative;
}
.pc-author_type_line {
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
}
.pc-author_type_line .pc-author__avatar {
  margin: 0;
}
.pc-author_type_line .pc-author__label {
  margin-top: 0;
}
.pc-author_type_line .pc-author__label:not(:first-child) {
  margin-left: 16px;
}
.pc-author_theme_dark .pc-author__description {
  color: var(--g-color-text-light-secondary);
}