.g-box {
  box-sizing: border-box;
}
.g-box_overflow_hidden {
  overflow: hidden;
}
.g-box_overflow_auto {
  overflow: auto;
}
.g-box_overflow_x {
  overflow-x: hidden;
  overflow-y: auto;
  overflow: hidden auto;
}
.g-box_overflow_y {
  overflow-x: auto;
  overflow-y: hidden;
  overflow: auto hidden;
}