.pc-storage-background-image {
  position: relative;
  overflow: hidden;
}
.pc-storage-background-image__img {
  left: 0;
  top: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}
.pc-storage-background-image__container {
  position: relative;
}