.ye-editor-sticky {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: 1fr;
}
.ye-editor-sticky_sticky {
  position: sticky;
  top: calc(0px + 8px);
  top: calc(var(--ye-toolbar-sticky-offset, 0px) + 8px);
}
.ye-editor-sticky_sticky-active:not(.ye-editor-sticky_clear) {
  z-index: 2000;
}
.ye-editor-sticky_sticky-active:not(.ye-editor-sticky_clear)::before {
  position: absolute;
  top: -4px;
  right: -4px;
  bottom: -4px;
  left: -4px;
  content: "";
  border: 1px solid var(--g-color-line-generic-solid);
  border-radius: 4px;
  background-color: var(--g-color-base-background);
}
.ye-editor-sticky_part_left:not(.ye-editor-sticky_clear).ye-editor-sticky_sticky::before {
  right: 0;
  border-right: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.ye-editor-sticky_part_right:not(.ye-editor-sticky_clear).ye-editor-sticky_sticky::before {
  left: 0;
  border-left: none;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}