.ye-wysiwyg-editor {
  display: flex;
  flex-direction: column;
}
.ye-wysiwyg-editor__toolbar {
  flex-shrink: 0;
}
.ye-wysiwyg-editor__editor {
  overflow-y: auto;
  flex-grow: 1;
}
.g-root_theme_dark .ye-wysiwyg-editor__editor .pm-iframe-container {
  background-color: var(--g-color-base-light);
}
.ye-wysiwyg-editor .yfm-editor {
  height: calc(100% - 4px - 4px);
  padding: 4px 4px 4px;
}
.ye-wysiwyg-editor_toolbar .yfm-editor {
  height: calc(100% - 16px - 16px);
  padding-top: 16px;
  padding-bottom: 16px;
}