.df-accordeon-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  box-shadow: 0 1px 5px var(--g-color-sfx-shadow);
  border-radius: 5px;
  color: var(--g-color-text-primary);
  background-color: var(--g-color-base-float);
}
.df-accordeon-card .df-row {
  width: 100%;
  max-width: none;
  max-width: initial;
}
.df-accordeon-card .df-row .df-select,
.df-accordeon-card .df-row .df-multi-select,
.df-accordeon-card .df-row .df-transparent_array-item {
  max-width: none;
  max-width: initial;
}
.df-accordeon-card .df-monaco-input,
.df-accordeon-card .df-monaco-base-view {
  width: 100%;
}
.df-accordeon-card:hover {
  box-shadow: 0 3px 10px var(--g-color-sfx-shadow);
}
.df-accordeon-card:last-child {
  margin-bottom: 0;
}
.df-accordeon-card_empty .df-accordeon-card__body,
.df-accordeon-card_empty .df-accordeon-card__header-toggle-btn {
  display: none;
}
.df-accordeon-card_empty .df-accordeon-card__header_open {
  border-bottom: none;
}
.df-accordeon-card__header {
  display: flex;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 16px;
}
.df-accordeon-card__header:hover {
  cursor: pointer;
}
.df-accordeon-card__header_open {
  border-bottom: 1px solid var(--g-color-line-generic);
}
.df-accordeon-card__header_without-action:hover {
  cursor: default;
}
.df-accordeon-card__header-content, .df-accordeon-card__header-toggle-btn {
  display: inline-flex;
  flex: 1 1 auto;
}
.df-accordeon-card__header-content {
  flex-direction: column;
  justify-content: center;
  order: -1;
}
.df-accordeon-card__header-content-description {
  margin-top: 12px;
  color: var(--g-color-text-complementary);
}
.df-accordeon-card__header-toggle-btn {
  flex-grow: 0;
  justify-content: flex-end;
  order: 1;
}
.df-accordeon-card__interal-actions {
  margin-right: 5px;
}
.df-accordeon-card__toggle-icon_open {
  transform: rotate(180deg);
}
.df-accordeon-card__body {
  display: none;
  padding: 16px;
}
.df-accordeon-card__body_open {
  display: block;
}