.df-row {
  display: flex;
  width: 500px;
  max-width: 500px;
  margin-bottom: 15px;
}
.df-row:last-child {
  margin-bottom: 0;
}
.df-row__left {
  width: 180px;
  min-height: 28px;
  display: flex;
  margin-bottom: auto;
  flex-direction: column;
  flex-shrink: 0;
}
.df-row__left-inner {
  display: inline;
  margin-top: auto;
  margin-bottom: auto;
}
.df-row__left::after {
  content: "";
  width: 100%;
  flex-shrink: 1;
}
.df-row__title {
  word-break: break-word;
  margin-right: 3px;
}
.df-row__title_required::after {
  content: "*";
  color: var(--g-color-text-danger);
}
.df-row__note {
  padding-right: 16px;
  position: relative;
}
.df-row__note-inner {
  position: absolute;
  margin-top: 1px;
}
.df-row__note-inner .g-help-popover {
  display: flex;
}
.df-row__note-inner .g-help-popover > span {
  display: flex;
}
.df-row__right {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-left: 15px;
}
.df-row__right-inner {
  display: flex;
  justify-content: space-around;
}
.df-row__description {
  margin-top: 10px;
  color: var(--g-color-text-secondary);
  word-break: break-word;
}
.df-row__remove-button {
  margin-left: 5px;
}
.df-row__required-mark {
  color: var(--g-color-text-danger);
}
.df-row__error-wrapper {
  min-width: 100%;
}