.ye-toolbar-button-with-popup-menu {
  width: 42px;
}
.ye-toolbar-button-with-popup-menu .g-button__text.g-button__text.g-button__text {
  margin: 0px 21px;
}
.ye-toolbar-button-with-popup-menu .g-button__icon_side_start {
  left: -1px;
}
.ye-toolbar-button-with-popup-menu .g-button__icon_side_end {
  right: -1px;
}
.ye-toolbar-button-with-popup-menu__menu-group .g-menu__group-label {
  color: var(--g-color-text-hint);
  font-size: var(--g-text-body-1-font-size);
  line-height: var(--g-text-body-1-line-height);
  font-weight: var(--g-text-body-font-weight);
}