.g-dialog {
  --_--side-padding: 32px;
  --_--close-button-space: 0px;
  position: relative;
  display: flex;
  flex-direction: column;
  width: var(--_--width, var(--g-dialog-width));
}
.g-dialog_has-scroll {
  overflow-y: auto;
  max-height: calc(100vh - 20px * 2);
  max-height: calc(100vh - var(--g-modal-margin, 20px) * 2);
}
.g-dialog_size_s {
  --_--width: 480px;
}
.g-dialog_size_m {
  --_--width: 720px;
}
.g-dialog_size_l {
  --_--width: 900px;
}
.g-dialog_has-close {
  --_--close-button-space: 24px;
}