.ye-image-placeholder {
  display: inline-flex;
  align-items: center;
  -moz-column-gap: 8px;
  column-gap: 8px;
  min-width: 300px;
  padding: 12px 20px;
  border-radius: 4px;
  background-color: var(--g-color-base-simple-hover-solid);
  font-size: var(--g-text-body-2-font-size);
  line-height: var(--g-text-body-2-line-height);
  font-weight: var(--g-text-body-font-weight);
}