/* use this for style redefinitions to awoid problems with
unpredictable css rules order in build */
.pc-file-link {
  align-items: baseline;
}
.pc-file-link_type_horizontal {
  display: flex;
}
.pc-file-link_type_horizontal .pc-file-link__file-label {
  margin-right: 8px;
}
.pc-file-link__file-label {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  width: 52px;
}
.pc-file-link__file-label .g-label__text {
  width: auto;
  width: initial;
}
.pc-file-link__link > a {
  color: var(--g-color-text-primary);
  border-radius: var(--g-focus-border-radius);
}
.pc-file-link__link > a:focus {
  outline: 2px solid var(--g-color-line-focus);
  outline-offset: 0;
}
.pc-file-link__link > a:focus:not(:focus-visible) {
  outline: 0;
}
.pc-file-link__link > a:hover {
  color: var(--g-color-text-secondary);
}
.pc-file-link_size_l .pc-file-link__file-label, .pc-file-link_size_m .pc-file-link__file-label {
  font-size: var(--g-text-body-2-font-size);
  line-height: var(--g-text-body-2-line-height);
}
.pc-file-link_size_xs .pc-file-link__file-label, .pc-file-link_size_s .pc-file-link__file-label {
  font-size: var(--g-text-body-1-font-size);
  line-height: var(--g-text-body-1-line-height);
}
.pc-file-link_theme_dark .pc-file-link__link > a {
  color: var(--g-color-text-light-primary);
}
.pc-file-link_theme_dark .pc-file-link__link > a:hover {
  color: var(--g-color-text-light-secondary);
}
.pc-file-link_theme_light .pc-file-link__link > a {
  color: var(--g-color-text-dark-primary);
}
.pc-file-link_theme_light .pc-file-link__link > a:hover {
  color: var(--g-color-text-dark-secondary);
}