/* use this for style redefinitions to awoid problems with
unpredictable css rules order in build */
.pc-overflow-scroller {
  display: flex;
  align-items: center;
  position: relative;
  overflow-x: hidden;
}
.pc-overflow-scroller__container {
  width: 100%;
  position: relative;
}
.pc-overflow-scroller__container_padding-left {
  padding-left: 24px;
}
.pc-overflow-scroller__container_padding-right {
  padding-right: 24px;
}
.pc-overflow-scroller__wrapper {
  position: relative;
  transition: left 0.6s;
}
.pc-overflow-scroller__arrow {
  display: inline-block;
  margin: 0;
  padding: 0;
  font: inherit;
  border: none;
  outline: none;
  color: inherit;
  background: none;
  cursor: pointer;
  position: absolute;
  z-index: 10;
  top: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 32px;
  height: calc(100% - 1px);
  cursor: pointer;
  color: var(--g-color-text-secondary);
  border-radius: var(--g-focus-border-radius);
}
.pc-overflow-scroller__arrow:focus {
  outline: 2px solid var(--g-color-line-focus);
  outline-offset: 0;
}
.pc-overflow-scroller__arrow:focus:not(:focus-visible) {
  outline: 0;
}
.pc-overflow-scroller__arrow_type_left {
  left: 0;
  transform: rotate(180deg);
}
.pc-overflow-scroller__arrow_type_right {
  right: 0;
}