.df-section {
  margin-bottom: 20px;
}
.df-section:last-child {
  margin-bottom: 0;
}
.df-section__header {
  margin-bottom: 20px;
}
.df-section__header_with-popover {
  display: flex;
  align-items: center;
}
.df-section__title {
  max-width: 533px;
  font-weight: 500;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.df-section__title_size_s {
  font-size: 13px;
}
.df-section__title_size_m {
  font-size: 15px;
}
.df-section__note {
  margin-left: 5px;
}
.df-section__note .g-help-popover {
  display: flex;
}
.df-section__note .g-help-popover > span {
  display: flex;
}
.df-section__description {
  margin-top: 4px;
}