.ye-editor-settings {
  display: flex;
  flex-wrap: nowrap;
}
.ye-editor-settings__mode-button {
  margin-right: 1px;
}
.ye-editor-settings__preview-button {
  margin: 0 2px;
}
.ye-editor-settings__separator {
  z-index: 0;
  margin: 6px 4px;
  border-left: 1px solid var(--g-color-line-generic);
}

.ye-settings-content {
  width: 300px;
}
.ye-settings-content__mode-help {
  float: right;
}
.ye-settings-content__mode-help > div {
  position: relative;
  top: 6px;
  display: inline;
}
.ye-settings-content__separator {
  border-bottom: 1px solid var(--g-color-line-generic);
}
.ye-settings-content__toolbar, .ye-settings-content__split-mode {
  padding: 8px 16px 12px;
}
.ye-settings-content__toolbar-hint {
  margin-top: 3px;
  padding-left: 19px;
  color: var(--g-color-text-secondary);
}
.ye-settings-content__version {
  position: absolute;
  bottom: 0px;
  left: 50%;
  opacity: 0.03;
  transform: translateX(-50%);
  font-family: var(--g-font-family-monospace);
  font-size: var(--g-text-code-inline-1-font-size);
  line-height: var(--g-text-code-inline-1-line-height);
  font-weight: var(--g-text-code-font-weight);
}