.ye-table-cell-view__left-button {
  position: absolute;
  z-index: 110;
  top: calc(50% - 14px);
  left: -13px;
  pointer-events: none;
  opacity: 0;
  border-radius: var(--g-border-radius-s);
  background: var(--g-color-base-background);
}
.ye-table-cell-view__left-button:hover {
  pointer-events: initial;
  opacity: 1;
}
.ye-table-cell-view__upper-button {
  position: absolute;
  z-index: 110;
  top: -13px;
  left: calc(50% - 14px);
  pointer-events: none;
  opacity: 0;
  border-radius: var(--g-border-radius-s);
  background: var(--g-color-base-background);
  transform: rotate(90deg);
}
.ye-table-cell-view__upper-button:hover {
  pointer-events: initial;
  opacity: 1;
}
.ye-table-cell-view__upper-visible .ye-table-cell-view__upper-button {
  pointer-events: initial;
  opacity: 1;
}
.ye-table-cell-view__left-visible .ye-table-cell-view__left-button {
  pointer-events: initial;
  opacity: 1;
}